.app__gallery{
    display: flex;
    flex-direction: row;

    background-image: radial-gradient(circle at 50% -20.71%, #613f23 0, #5c3823 8.33%, #533022 16.67%, #47271e 25%, #381d19 33.33%, #271311 41.67%, #140000 50%, #000000 58.33%, #000000 66.67%, #000000 75%, #000000 83.33%, #000000 91.67%, #000000 100%);
    padding: 4rem 0;
    padding: 8rem 0 4rem 6rem;
}


.app__gallery-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    min-width: 100px;
    padding-right: 2rem;
}

.app__gallery-content button{
    margin-bottom: 2rem;
    
}

.app__gallery-images{
    flex: 1;
    display: flex;
    flex-direction: row;
    max-width: 50%;
    position: relative;

}

.app__gallery-images_container{
    display: flex;
    flex-direction: row;
    width: max-content;
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.app__gallery-images_container::-webkit-scrollbar{
    display: none;

}
.app__gallery-images_card{
    position: relative;
    min-width: 301px;
    height: 447px;
    margin-right: 1.5rem;
    
}

.gallery__image-icon{
    position: absolute;
    color: #faf4f4;
    font-size: 2rem;
    opacity: 0.5;
    transition: .5s ease;
    cursor: pointer;
    
}

.app__gallery-images_card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 2;
    transition: 0.9s ease;
    border-radius: 5%;
    cursor: pointer;
}



.app__gallery-images_card:hover img{
    
    opacity: 0.35;
    
    transform: scale(1.1);
    
    
} 
.app__gallery-images_card:hover .gallery__image-icon{
    
    opacity: 1;
    border-radius: 5%;
    
} 

.app__gallery-images_arrow{
    
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0 1rem;
    position: absolute;
    bottom: 5%;
}



.gallery__arrow-icon{
    /* color: var(--color-golden); */
    font-size: 2rem;
    cursor: pointer;
    opacity: 0.5;
    background-color: var(--color-black);
    
    
}

.gallery__arrow-icon:hover{
    color: var(--color-white);
    
    
}

@media screen and (min-width: 2000px){
    .app__gallery-content{
        min-width: 1000px;
        padding-right: 4rem;
    }
    .app__gallery-images_card{
        min-width: 400px;
        height: 550px;
        border-radius: 5%;
    }
}

@media screen and (max-width: 1150px){
    .app__gallery{
        flex-direction: column;
    }

    
    .app__gallery-images{
        max-width: 100%;
        margin: 2rem 0;
    }

    
}


@media screen and (max-width: 850px){
    .app__gallery{
        padding: 4rem 0 4rem 6rem;
    }
    
}


@media screen and (max-width: 650px){
    .app__gallery{
        padding: 4rem 0 4rem 2rem;
    }
    .app__gallery-content{
        min-width: 100%;
    }
    .app__gallery-images_card{
        min-width: 240px;
        height: 320px;
    }
    
}
