.app__footer {
    width: 100%;
    position: relative;
    z-index: 1;
    
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;

    /* background: black; */
    background-image: radial-gradient(circle at 50% -20.71%, #613f23 0, #5c3823 8.33%, #533022 16.67%, #47271e 25%, #381d19 33.33%, #271311 41.67%, #140000 48%, #000000 58.33%, #000000 66.67%, #000000 75%, #000000 83.33%, #000000 91.67%, #000000 100%);
    padding-top: 0;
}

.app__footer-links {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    margin-top: 5rem;
    padding: 0 2rem;
}

.app__footer-links_contact,
.app__footer-links_logo,
.app__footer-links_work{
    flex: 1;
    margin: 1rem;
    text-align: center;

}

.app__footer-links_logo img:first-child{
    width: 140px;
    margin-bottom: 0.90rem;
}
.app__footer-links_work p:nth-child(2n + 1){
    margin-bottom: 1rem;
}
.app__footer-links_icons{
    margin-top: 0.5rem;
}
.app__footer-links_icons svg{
    color: aliceblue;
    margin: 0.5rem;
    font-size: 24px;
    cursor: pointer;
}

.app__footer-links_icons svg:hover{
    color: var(--color-golden)
}

.app__footer-headtext{
    font-family: var(--font-base);
    color: var(--color-white);
    font-weight: 400;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    font-size: 32px;
    line-height: 41.6px;
    margin-bottom: 1rem;
}

.footer__copyright{
    margin-top: 3rem;
}

@media screen and (min-width:2000px) {
    .app__footer-headtext{
        font-size: 51px;
        line-height: 61px;
    }
}
@media screen and (max-width:1150px) {
    .app__footer-links{
        align-items: center;
        flex-direction: column;
        padding: 0;
    }


    .app__footer-links_contact,
    .app__footer-links_logo,
    .app__footer-links_work{
    
        margin: 1rem 0;
        width: 100%;

    }

}

@media screen and (max-width:650px) {
    .app__footer{
        padding: 0 0 2rem 0;
    }
}
@media screen and (max-width:350px) {
    .app__footer-links_logo img:first-child{
        width: 80%;
    }
}